.navbar{display:flex;align-items:center;justify-content:space-between;position:fixed;top:0;left:0;right:0;padding:10px 5%;z-index:111;background:transparent;transition:0.3s}.navbar img{height:50px;cursor:pointer}.navbar ul{display:grid;grid-template-columns:repeat(3, 1fr)}.navbar ul li{display:flex;align-items:center;justify-content:center;padding:10px;cursor:pointer}.navbar ul li .icon{display:none}.navbar ul li.active{color:#00045f}.nav__sticky{background:#fff;box-shadow:0 0 8px rgba(16,16,16,0.3)}@media (max-width: 425px){.navbar{height:50px;padding:0 5%;z-index:1111;background:#fff}.navbar .navbar__brand img{vertical-align:middle;width:80px}.navbar ul{position:fixed;bottom:0;left:0;right:0;height:50px;background:#fff;box-sizing:border-box;display:grid;grid-template-columns:repeat(3, 1fr);box-shadow:0 -2px 8px rgba(16,16,16,0.1)}.navbar ul li{flex-direction:column;cursor:pointer}.navbar ul li:hover{background:rgba(0,4,95,0.1)}.navbar ul li.active a{color:#00045f;align-items:center}.navbar ul .icon{display:block !important}.navbar ul a{display:flex;align-items:center;justify-content:center;color:#a2a2a2;flex-direction:column}}

.footer__foo{display:flex;align-items:center;justify-content:space-between;margin:0 10%;padding:2rem 0;border-top:2px solid #a2a2a2}.footer__foo .footer__brand img{width:90%}.footer__foo .footer__section{display:flex;align-items:center;justify-content:space-between;flex-wrap:wrap;align-items:flex-start !important;width:60%;margin:1rem}.footer__foo .footer__section h6{margin-bottom:10px}.footer__foo .footer__section li:not(:first-child) .list{display:flex}.footer__foo .footer__section .list li{cursor:pointer}.footer__foo .footer__section .list li:not(:last-child){margin-right:1rem}.footer__foo .footer__section .list img{width:1.5em}.footer__footer{display:flex;align-items:center;justify-content:center;height:4rem;color:#fff;background:#101010}.footer__footer p{margin-left:5px}@media (max-width: 768px){.footer__foo{flex-direction:column}.footer__foo .footer__section{margin-top:2rem;width:100%}}@media (max-width: 425px){.footer__footer{margin-bottom:3rem}}

.projects .title{width:40%;padding-left:10%}.projects .title p{margin:1rem 0 2rem 0}.projects .title ul{display:flex}.projects .title ul li{position:relative;cursor:pointer;padding:0 10px}.projects .title ul li:not(:last-child){margin-right:1rem}.projects .title ul .active::before{content:"";position:absolute;bottom:5px;left:0;width:100%;height:1px;border-bottom:3px solid #f0a410}.projects .box__container{width:90%;margin:1rem auto;display:grid;grid-gap:20px;grid-template-columns:repeat(auto-fill, minmax(400px, 1fr))}.projects .box__container .project{position:relative;cursor:pointer}.projects .box__container .project .project__title{position:absolute;top:0;left:0;right:0;padding:5%;color:#fff}.projects .box__container .project:hover .hover{display:block}.projects .box__container .project .hover{content:"";width:100%;height:98.5%;position:absolute;background:rgba(16,16,16,0.7);display:none}.projects .box__container .project .hover p:last-child{margin-top:1rem}.projects .box__container .project .hover .project__link{display:flex;align-items:center;justify-content:space-between;width:100%;padding:0 1rem;position:absolute;bottom:1rem}.projects .box__container .project .hover .project__link a{color:#fff}.projects .box__container img{width:100%}.projects .btn__view{cursor:pointer;text-align:center;color:#00045f}@media (max-width: 1024px){.projects .title{width:60%}}@media (max-width: 768px){.projects .title{width:70%}}@media (max-width: 425px){.projects .title{width:100%}.projects .box__container .project__title p:last-child{max-height:5rem;overflow:scroll}}

.certificates__wrapper{position:absolute;right:0;width:49%;height:100%}.certificates__wrapper .box__certificate{display:flex;align-items:center;justify-content:center;flex-direction:column;position:absolute;cursor:pointer;width:230px;height:230px;padding:1rem;border-radius:8px;box-shadow:0 0 8px rgba(16,16,16,0.3)}.certificates__wrapper .box__certificate h5{text-align:center}.certificates__wrapper .website{top:5rem;left:3rem}.certificates__wrapper .android{right:0;bottom:0}@media (max-width: 1024px){.box__certificate{padding:1.5rem !important}.website{left:0 !important}.android{right:0 !important}}@media (max-width: 900px){.certificates__wrapper{width:100%;margin-top:3rem;position:unset}.certificates__wrapper .certificates{display:flex;align-items:center;justify-content:space-around;flex-wrap:wrap}.certificates__wrapper .box__certificate{position:unset;margin:1rem}.certificates__wrapper .website{-webkit-transform:unset;transform:unset}}

.jumbotron{display:flex;align-items:center;justify-content:space-between;position:relative;margin-top:5rem !important}.jumbotron .section{width:55%}.jumbotron .section h3{color:#222}.jumbotron .section h1{color:#f0a410}.jumbotron .section p{color:#a2a2a2;margin-top:1rem}.jumbotron .section .button ul{display:flex;align-items:center;flex-wrap:wrap;height:8rem;margin-top:2rem}.jumbotron .section .button ul li:last-child a{color:#a2a2a2}.jumbotron .section .button ul li:first-child a{margin-right:2rem;padding:1rem 3rem;border-radius:8px;color:#fff;background:#00045f}.jumbotron .section .button ul li a:not(:last-child){margin-right:2.5rem}.jumbotron .image{width:40%}.jumbotron .image img{width:100%}.jumbotron .decoration{width:100%;height:100%;background:url(/static/media/Vector1.1c9b439e.svg);background-repeat:no-repeat;background-size:contain;position:absolute;left:-50%;bottom:-20%;z-index:-1;opacity:0.3}@media (max-width: 1024px){.jumbotron{margin:1rem 5%}}@media (max-width: 900px){.jumbotron{margin:0 !important;margin-top:5rem !important;flex-direction:column-reverse}.jumbotron .section{width:80%;margin-top:3rem}.jumbotron .image{width:50%;text-align:center}.jumbotron .image img{width:100%}.jumbotron .decoration{height:50%}}@media (max-width: 425px){.jumbotron .section h1{text-shadow:0 0 4px rgba(255,255,255,0.7)}.jumbotron .image{width:70%}}

.container{display:flex;align-items:center;justify-content:space-between;position:relative}.container .skills{width:45%}.container .skills .title p{margin:1rem 0 2rem 0}.container .skills .skill__information{display:flex;align-items:center;justify-content:space-between;margin-top:1rem}.container .skills .skill__bar{position:relative;width:100%;height:2px;margin-top:0.5rem;background:#a2a2a2}.container .skills .active::before{content:"";position:absolute;-webkit-transform:translateY(-2px);transform:translateY(-2px);width:calc(0 * 1%);width:calc(var(--width, 0) * 1%);height:6px;background:#f0a410;-webkit-animation:skillbar 1.5s ease-out;animation:skillbar 1.5s ease-out}@-webkit-keyframes skillbar{0%{width:0}100%{width:calc(0 * 1%);width:calc(var(--width, 0) * 1%)}}@keyframes skillbar{0%{width:0}100%{width:calc(0 * 1%);width:calc(var(--width, 0) * 1%)}}.container .decoration{width:100%;height:100%;background:url(/static/media/Vector1.1c9b439e.svg);background-repeat:no-repeat;background-size:contain;position:absolute;z-index:-1;left:auto;bottom:auto;right:auto;top:auto;opacity:0.3;top:-70% !important;right:-70% !important}.container .decoration1{width:100%;height:100%;background:url(/static/media/Vector.55d99c53.svg);background-repeat:no-repeat;background-size:contain;position:absolute;z-index:-1;left:auto;bottom:auto;right:auto;top:auto;opacity:0.3;left:-40%;bottom:-110%}@media (max-width: 900px){.container{flex-direction:column}.container .skills{width:100%}}

.contact{position:relative}.contact .section{display:flex;align-items:center;justify-content:space-between}.contact .section .box__information{display:flex;margin-top:2rem}.contact .section .box__information .icon{display:flex;align-items:center;justify-content:center;width:40px;height:40px;border-radius:50%;margin-right:15px;color:#fff !important;background:#a2a2a2}.contact .section .box__information .address__information{width:80%}.contact form{width:49%;display:flex;flex-direction:column}.contact form input,.contact form textarea,.contact form button{margin-top:1rem;padding:10px 15px}.contact form button{width:25%;border:none;cursor:pointer;outline:inherit;color:#fff;background:#00045f}@media (max-width: 1024px){.contact form button{width:40%}}@media (max-width: 768px){.contact .section{flex-direction:column}.contact .section .contact__information{width:100%}.contact form{width:100%;margin:1rem}.contact form button{width:25%}}@media (max-width: 425px){.contact form{width:100%;margin:1rem}.contact form button{width:40%}}

h2{text-align:center}.hire__contain{display:grid;grid-template-columns:repeat(3, 1fr);grid-gap:2em}.hire__contain .box__hire{display:flex;align-items:center;flex-direction:column;text-align:center;margin-top:3em}.hire__contain .box__hire .icon__hire{display:flex;align-items:center;justify-content:center;width:7em;height:7em;border-radius:50%;margin-bottom:1em;background:#a2a2a2}.hire__contain .box__hire .icon__hire .icon{font-size:3em;color:#fff}@media (min-width: 1600px){.icon__hire{width:12em !important;height:12em !important}.icon__hire .icon{font-size:5em !important}}@media (max-width: 768px){.icon__hire{width:5em !important;height:5em !important}.icon__hire .icon{font-size:2em !important}}@media (max-width: 425px){.hire__contain{grid-template-columns:1fr}.hire__contain .icon__hire{width:6em !important;height:6em !important}.hire__contain .icon__hire .icon{font-size:2.5em !important}}

.web__certificate{display:grid;padding:2em;grid-gap:2em;grid-template-columns:repeat(auto-fit, minmax(350px, 1fr))}.web__certificate img{width:100%;box-shadow:0 4px 10px rgba(16,16,16,0.3)}.flip{min-width:290px;min-height:310px;-webkit-perspective:1000px;perspective:1000px}.flip .flip__inner{position:relative;transition:-webkit-transform 0.6s;transition:transform 0.6s;transition:transform 0.6s, -webkit-transform 0.6s;-webkit-transform-style:preserve-3d;transform-style:preserve-3d}.flip .flip__inner .flip__front,.flip .flip__inner .flip__back{position:absolute;-webkit-backface-visibility:hidden;backface-visibility:hidden}.flip .flip__inner .flip__back{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}@media (max-width: 1177px){.flip{min-height:400px}}@media (max-width: 795px){.flip{min-height:550px}}@media (max-width: 650px){.flip{min-height:420px}}@media (max-width: 500px){.flip{min-height:320px}}@media (max-width: 425px){.web__certificate{padding:1em;grid-gap:1em;grid-template-columns:repeat(auto-fit, minmax(290px, 1fr))}.web__certificate img{border:1rem solid #101010}.flip{min-height:290px}}@media (max-width: 375px){.flip{min-height:250px}}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Poppins", sans-serif}.App{overflow:hidden}h1{font-size:4em}h2{font-size:3em}h3{font-size:2em}h4{font-size:1.5em}h5{font-size:1.3em}h6{font-size:1em}a{text-decoration:none;color:#101010}ul{list-style:none}.jumbotron,.container,.hireme,.contact{margin:1rem 10%}.divider{height:5em}@media (max-width: 1300px){h1{font-size:3em}h2{font-size:2.5em}h3{font-size:1.8em}h4{font-size:1.4em}h5{font-size:1.2em}h6{font-size:1.1em}a,p,span,.icon{font-size:1em}}@media (max-width: 1000px){h1{font-size:2.5em}h2{font-size:1.8em}h3{font-size:1.4em}h4{font-size:1.2em}h5{font-size:1em}h6{font-size:0.8em}p,span,.icon{font-size:0.8em}}

